import React, { useState } from 'react';
import {
    Box,
    Paper,
    Typography,
    Button,
    TextField,
    Alert,
    Divider,
    Grid,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    IconButton
} from '@mui/material';
import {
    MedicalServices as MedicalIcon,
    Security as SecurityIcon,
    Shortcut as ShortcutIcon,
    Close as CloseIcon
} from '@mui/icons-material';
import { autoWebViewJs } from '@droid/android';

/**
 * 桌面快捷方式配置组件
 * 用于创建医疗呼救和安全呼救的桌面快捷方式
 */
const ShortcutConfig: React.FC = () => {
    const [medicalName, setMedicalName] = useState('意外呼救');
    const [securityName, setSecurityName] = useState('安全呼救');
    const [isCreating, setIsCreating] = useState(false);
    const [message, setMessage] = useState<{type: 'success' | 'error' | 'info' | 'warning', text: string} | null>(null);
    
    const [isMedicalDialogOpen, setIsMedicalDialogOpen] = useState(false);
    const [isSecurityDialogOpen, setIsSecurityDialogOpen] = useState(false);

    const handleOpenMedicalDialog = () => setIsMedicalDialogOpen(true);
    const handleCloseMedicalDialog = () => setIsMedicalDialogOpen(false);
    
    const handleOpenSecurityDialog = () => setIsSecurityDialogOpen(true);
    const handleCloseSecurityDialog = () => setIsSecurityDialogOpen(false);

    // 创建医疗呼救快捷方式
    const handleCreateMedicalShortcut = async () => {
        if (!medicalName.trim()) {
            setMessage({type: 'error', text: '请输入快捷方式名称'});
            return;
        }

        setIsCreating(true);
        setMessage(null);

        try {
            const supportScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.isShortcutSupported();`;
            const {javaResultString: supportResult} = autoWebViewJs.callScript(supportScript);
            
            if (supportResult !== 'true') {
                setMessage({type: 'error', text: '当前设备不支持创建桌面快捷方式'});
                return;
            }

            const script = `com.fanfanlo.emergencycall.manager.ShortcutManager.createMedicalShortcut("${medicalName}");`;
            const {javaResultString} = autoWebViewJs.callScript(script);
            
            if (javaResultString === 'true') {
                setMessage({type: 'success', text: '医疗呼救快捷方式创建成功！请检查您的桌面。'});
                handleCloseMedicalDialog();
            } else {
                setMessage({type: 'error', text: '快捷方式创建失败，请检查权限设置'});
            }
        } catch (error) {
            console.error('Error creating medical shortcut:', error);
            setMessage({type: 'error', text: '创建快捷方式时发生错误'});
        } finally {
            setIsCreating(false);
        }
    };

    // 创建安全呼救快捷方式
    const handleCreateSecurityShortcut = async () => {
        if (!securityName.trim()) {
            setMessage({type: 'error', text: '请输入快捷方式名称'});
            return;
        }

        setIsCreating(true);
        setMessage(null);

        try {
            const supportScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.isShortcutSupported();`;
            const {javaResultString: supportResult} = autoWebViewJs.callScript(supportScript);
            
            if (supportResult !== 'true') {
                setMessage({type: 'error', text: '当前设备不支持创建桌面快捷方式'});
                return;
            }

            const script = `com.fanfanlo.emergencycall.manager.ShortcutManager.createSecurityShortcut("${securityName}");`;
            const {javaResultString} = autoWebViewJs.callScript(script);
            
            if (javaResultString === 'true') {
                setMessage({type: 'success', text: '安全呼救快捷方式创建成功！请检查您的桌面。'});
                handleCloseSecurityDialog();
            } else {
                setMessage({type: 'error', text: '快捷方式创建失败，请检查权限设置'});
            }
        } catch (error) {
            console.error('Error creating security shortcut:', error);
            setMessage({type: 'error', text: '创建快捷方式时发生错误'});
        } finally {
            setIsCreating(false);
        }
    };
    
    // 检查并请求权限
    const handleRequestPermission = async () => {
        try {
            const script = `com.fanfanlo.emergencycall.manager.ShortcutManager.checkAndRequestPermissionWithResult();`;
            const {javaResultString: result} = autoWebViewJs.callScript(script);
            
            if (result && result !== 'undefined') {
                setMessage({
                    type: result.startsWith('✅') ? 'success' : result.startsWith('❌') ? 'error' : 'info', 
                    text: result
                });
            } else {
                throw new Error('方法返回undefined，可能需要重新编译');
            }
        } catch (error) {
            console.error('权限引导调用出错:', error);
            setMessage({
                type: 'warning', 
                text: `⚠️ 权限引导失败，请尝试手动开启。

请前往：设置 > 应用管理 > 紧急呼救 > 权限管理，然后开启“创建桌面快捷方式”权限。`
            });
        }
    };

    return (
        <Box sx={{ p: 0, mt: 2 }}>
            <Paper elevation={2} sx={{ p: 2, mb: 0 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <ShortcutIcon sx={{ mr: 1.5, color: 'primary.main' }} />
                    <Typography variant="h5" component="h1">
                        桌面快捷方式
                    </Typography>
                </Box>
                
                <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                    将呼救功能添加到桌面，危急时刻一键启动。
                </Typography>

                {message && (
                    <Alert 
                        severity={message.type} 
                        sx={{ mb: 2, whiteSpace: 'pre-wrap' }}
                        onClose={() => setMessage(null)}
                    >
                        {message.text}
                    </Alert>
                )}

                <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                        <Button
                            fullWidth
                            variant="contained"
                            color="error"
                            onClick={handleOpenMedicalDialog}
                            startIcon={<MedicalIcon />}
                            sx={{ py: 1.5 }}
                        >
                            创建医疗快捷方式
                        </Button>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Button
                            fullWidth
                            variant="contained"
                            color="warning"
                            onClick={handleOpenSecurityDialog}
                            startIcon={<SecurityIcon />}
                            sx={{ py: 1.5 }}
                        >
                            创建安全快捷方式
                        </Button>
                    </Grid>
                </Grid>

                {/* 医疗呼救弹窗 */}
                <Dialog open={isMedicalDialogOpen} onClose={handleCloseMedicalDialog} fullWidth maxWidth="xs">
                    <DialogTitle>
                        创建医疗呼救快捷方式
                        <IconButton
                            aria-label="close"
                            onClick={handleCloseMedicalDialog}
                            sx={{ position: 'absolute', right: 8, top: 8, color: (theme) => theme.palette.grey[500] }}
                        >
                            <CloseIcon />
                        </IconButton>
                    </DialogTitle>
                    <DialogContent>
                        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                            用于突发疾病、意外受伤等需要医疗救助的场景。
                        </Typography>
                        <TextField
                            autoFocus
                            margin="dense"
                            label="快捷方式名称"
                            type="text"
                            fullWidth
                            variant="outlined"
                            value={medicalName}
                            onChange={(e) => setMedicalName(e.target.value)}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleCloseMedicalDialog}>取消</Button>
                        <Button onClick={handleCreateMedicalShortcut} variant="contained" color="error" disabled={isCreating}>
                            {isCreating ? '创建中...' : '确认创建'}
                        </Button>
                    </DialogActions>
                </Dialog>

                {/* 安全呼救弹窗 */}
                <Dialog open={isSecurityDialogOpen} onClose={handleCloseSecurityDialog} fullWidth maxWidth="xs">
                    <DialogTitle>
                        创建安全呼救快捷方式
                        <IconButton
                            aria-label="close"
                            onClick={handleCloseSecurityDialog}
                            sx={{ position: 'absolute', right: 8, top: 8, color: (theme) => theme.palette.grey[500] }}
                        >
                            <CloseIcon />
                        </IconButton>
                    </DialogTitle>
                    <DialogContent>
                        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                            用于遇到危险、需要紧急援助的场景（开发中）。
                        </Typography>
                        <TextField
                            autoFocus
                            margin="dense"
                            label="快捷方式名称"
                            type="text"
                            fullWidth
                            variant="outlined"
                            value={securityName}
                            onChange={(e) => setSecurityName(e.target.value)}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleCloseSecurityDialog}>取消</Button>
                        <Button onClick={handleCreateSecurityShortcut} variant="contained" color="warning" disabled={isCreating}>
                            {isCreating ? '创建中...' : '确认创建'}
                        </Button>
                    </DialogActions>
                </Dialog>

                <Divider sx={{ my: 2 }} />

                {/* 使用说明 */}
                <Box sx={{ mt: 2, p: 2, bgcolor: 'grey.100', borderRadius: 2 }}>
                    <Typography variant="subtitle1" gutterBottom>
                        <strong>使用说明</strong>
                    </Typography>
                    <ul style={{ margin: 0, paddingLeft: '20px', fontSize: '0.9rem' }}>
                        <li>点击上方按钮，选择您需要的呼救类型来创建快捷方式。</li>
                        <li><strong>重要：</strong>部分手机（如小米、华为）需要手动开启“创建桌面快捷方式”权限。</li>
                        <li>如果创建失败，请点击下方按钮尝试自动跳转到权限设置页面。</li>
                    </ul>
                    
                    <Box sx={{ mt: 2, p: 1.5, bgcolor: 'rgba(255, 167, 38, 0.1)', borderRadius: 1, border: '1px solid', borderColor: 'rgba(255, 167, 38, 0.3)' }}>
                        <Typography variant="body2" sx={{ color: '#4D3B18', fontWeight: '500' }}>
                            <strong>ℹ️ 温馨提示：</strong>
                            若创建失败，请点击下方“打开权限设置”按钮，系统将尝试引导您至相关设置页面。
                        </Typography>
                    </Box>
                </Box>
                
                <Button
                    fullWidth
                    variant="outlined"
                    color="primary"
                    onClick={handleRequestPermission}
                    disabled={isCreating}
                    sx={{ mt: 2 }}
                >
                    打开权限设置
                </Button>
            </Paper>
        </Box>
    );
};

export default ShortcutConfig;